import React, { useState, useEffect, useCallback, useRef } from "react";
import { withRouter } from "react-router-dom";
import axios from "axios";
import { DataGrid, GridToolbar } from "@mui/x-data-grid";
import style from "./style.css";
import { Select, MenuItem } from "@mui/material";
import { Button, Modal, Form, Row, Col, Container } from "react-bootstrap";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import XLSX from "xlsx";
import Dialog from "@mui/material/Dialog";
import Box from "@mui/material/Box";
import DialogContent from "@mui/material/DialogContent";
import CircularProgress from "@mui/material/CircularProgress";

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const EXTENSIONS = ["csv", "xls", "xlsx"];

function RequestHandoverChangesBody() {
  let [items, setItems] = useState([]);
  let [item, setItem] = useState([]);
  let [properties, setProperties] = useState([]);
  let [headerProperties, setHeaderProperties] = useState([]);
  let [open, setOpen] = useState(false);
  let [severity, setSeverity] = useState("");
  let [alart, setAlart] = useState("");
  let [comment, setComment] = useState("");
  let [commentStatus, setCommentStatus] = useState("");
  let [status, setStatus] = useState("");
  let [continuesStatus, setContinuesStatus] = useState("");
  let [errors, setErrors] = useState({});
  let [newProperties, setNewProperties] = useState([]);
  let [projectsData, setProjectsData] = useState([]);
  let [headersConfig, setHeadersConfig] = useState([]);
  let [rates, setRates] = useState([]);
  let [openDialog, setOpenDialog] = useState(false);
  let [mobitelUsersName, setMobitelUsersName] = useState([]);
  let [changeHandoverApprover, setHandoverChangeApprover] = useState();
  let [projectManager, setProjectManager] = useState();
  let [handoverChangeApproverName, setHandoverChangeApproverName] = useState();

  const handleChange = (e, index) => {
    setProperties({ ...properties, [index]: e.target.value });
  };

  const [singleTask, setSingleTask] = useState(false);
  const [bulkTask, setBulkTask] = useState(false);
  const fileRef = useRef();

  const handleClose = () => setSingleTask(false);
  const bulkHandleClose = () => {
    setRates([]);
    setNewProperties([]);
    setBulkTask(false);
  };
  const handleSingleTask = (row) => {
    setItem(row);
    for (var i = row.taskHistory.length - 2; i >= 0; i--) {
      if (row.taskHistory[i + 1].taskStatus == "Hold requested") {
        setContinuesStatus(row.taskHistory[i].taskStatus);
        break;
      }
    }
    setProperties(row.properties);
    setHeaderProperties(row.headerProperties);
    setStatus(row.taskStatus);
    setComment("");
    setSingleTask(true);
  };
  const axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL,
  });

  useEffect(() => {
    getMobitelUserName();

    setOpenDialog(true);
    getData();

    axiosInstance
      .get("projonline/project", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        setProjectsData(res.data.projTemplates);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  useEffect(() => {
    setHandoverChangeApproverfunction();
  }, [changeHandoverApprover]);

  const getMobitelUserName = () => {
    axiosInstance
      .get("projonline/mobitelusers", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        const mobitelUsersName = res.data.map((user) => ({
          name: user.name,
          _id: user._id,
        }));

        // console.log(mobitelUsersName);
        setMobitelUsersName(mobitelUsersName);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const getData = () => {
    axiosInstance
      .get("projonline/gettasks", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        const data = res.data.tasks.filter((x) => {
          if (x.taskStatus != "Withdraw") {
            return x;
          }
        });
        setItems(data);

        setOpenDialog(false);

        return;
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const getProjectManager = (assignedProjectManager) => {
    let projectManager = null;
    mobitelUsersName.forEach((obj) => {
      if (obj._id === assignedProjectManager) {
        projectManager = obj.name;
      }
    });

    // Update the state after the loop
    setProjectManager(projectManager);
  };

  const setHandoverChangeApproverfunction = () => {
    let HandoverChangeApproverName = null;

    mobitelUsersName.forEach((obj) => {
      if (obj._id === changeHandoverApprover) {
        HandoverChangeApproverName = obj.name;
      }
    });

    // console.log(HandoverChangeApproverName);
    setHandoverChangeApproverName(HandoverChangeApproverName);
  };

  const findFormErrors = () => {
    const newErrors = {};
    // name errors
    if (!comment || comment === "") newErrors.comment = "cannot be blank!";
    // console.log(newErrors)
    return newErrors;
  };
  const findFormErrorsStatus = () => {
    const newErrors = {};
    // name errors
    if (!commentStatus || commentStatus === "")
      newErrors.commentStatus = "cannot be blank!";
    // console.log(newErrors)
    return newErrors;
  };
  const handleSubmit = () => {
    const newErrors = findFormErrors();
    if (Object.keys(newErrors).length > 0) {
      // We got errors!
      setErrors(newErrors);
    } else {
      axiosInstance
        .post(
          "projonline/addhandoverchanges",
          {
            taskRef: item.taskRef,
            siteID: item.siteID,
            siteName: item.siteName,
            newProperties: properties,
            existingProperties: item.properties,
            headerProperties: headerProperties,
            project: item.project,
            assignedProjectManager: item.assignedProjectManager,
            subProject: item.subProject,
            taskRefID: item._id,
            status: "Pending",
            changeHandOverUser: changeHandoverApprover,
            comment: comment,
          },
          {
            headers: {
              token:
                "Bearer " +
                JSON.parse(localStorage.getItem("user")).accessToken,
            },
          }
        )
        .then((res) => {
          // console.log(res)
        })
        .catch((err) => {
          console.log(err);
        });

      axiosInstance
        .put(
          `projonline/update/${item._id}`,
          {
            taskObject: {},
            taskHistory: {
              taskStatus: "Handover change requested",
              comment: comment,
            },
          },
          {
            headers: {
              token:
                "Bearer " +
                JSON.parse(localStorage.getItem("user")).accessToken,
            },
          }
        )
        .then((res) => {
          // console.log(res)
          setOpen(true);
          setSeverity("success");
          setAlart("Handover successfully requested");
          setSingleTask(false);
          return;
        })
        .catch((err) => {
          // console.log(err)
          setOpen(true);
          setSeverity("error");
          setAlart("Error occured");
        });
    }
  };

  const bulkSubmit = () => {
    const newErrors = findFormErrors();
    if (Object.keys(newErrors).length > 0) {
      // We got errors!
      setErrors(newErrors);
    } else {
      newProperties.forEach((prop) => {
        const temp = items.filter((it) => prop[0] == it.taskRef);

        let newProperties = prop;
        newProperties.splice(0, 1);
        axiosInstance
          .post(
            "projonline/addhandoverchanges",
            {
              taskRef: temp[0].taskRef,
              siteID: temp[0].siteID,
              siteName: temp[0].siteName,
              existingProperties: temp[0].properties,
              newProperties: newProperties,
              headerProperties: temp[0].headerProperties,
              project: temp[0].project,
              assignedProjectManager: temp[0].assignedProjectManager,
              subProject: temp[0].subProject,
              taskRefID: temp[0]._id,
              status: "Pending",
              taskStatus: status,
              comment: commentStatus,
            },
            {
              headers: {
                token:
                  "Bearer " +
                  JSON.parse(localStorage.getItem("user")).accessToken,
              },
            }
          )
          .then((res) => {
            // console.log(res)
          })
          .catch((err) => {
            console.log(err);
          });

        axiosInstance
          .put(
            `projonline/update/${temp[0]._id}`,
            {
              taskObject: {},
              taskHistory: {
                taskStatus: "Handover change requested",
                comment: commentStatus,
              },
            },
            {
              headers: {
                token:
                  "Bearer " +
                  JSON.parse(localStorage.getItem("user")).accessToken,
              },
            }
          )
          .then((res) => {
            // console.log(res)
            setOpen(true);
            setComment("");
            setSeverity("success");
            setAlart("Handover successfully requested");
            bulkHandleClose();
            return;
          })
          .catch((err) => {
            // console.log(err)
            setComment("");
            setOpen(true);
            setSeverity("error");
            setAlart("Error occured");
          });
      });
    }
  };

  function arrayEquals(a, b) {
    return (
      Array.isArray(a) &&
      Array.isArray(b) &&
      a.length === b.length &&
      a.every((val, index) => val === b[index])
    );
  }

  const changeStatus = () => {
    const newErrors = findFormErrorsStatus();
    if (Object.keys(newErrors).length > 0) {
      // We got errors!
      setErrors(newErrors);
    } else if (status != item.taskStatus) {
      axiosInstance
        .post(
          "projonline/addhandoverchanges",
          {
            taskRef: item.taskRef,
            siteID: item.siteID,
            siteName: item.siteName,
            newProperties: properties,
            existingProperties: item.properties,
            headerProperties: headerProperties,
            project: item.project,
            assignedProjectManager: item.assignedProjectManager,
            subProject: item.subProject,
            taskRefID: item._id,
            status: "Pending",
            taskStatus: status,
            changeHandOverUser: changeHandoverApprover,
            comment: `Approver: ${handoverChangeApproverName} / Reason: ${commentStatus}`,
          },
          {
            headers: {
              token:
                "Bearer " +
                JSON.parse(localStorage.getItem("user")).accessToken,
            },
          }
        )
        .then((res) => {
          // console.log(res)
        })
        .catch((err) => {
          console.log(err);
        });

      axiosInstance
        .put(
          `projonline/update/${item._id}`,
          {
            taskObject: {},
            taskHistory: {
              taskStatus: `${status} requested`,
              comment: `Approver: ${handoverChangeApproverName} / Reason: ${commentStatus}`,
            },
          },
          {
            headers: {
              token:
                "Bearer " +
                JSON.parse(localStorage.getItem("user")).accessToken,
            },
          }
        )
        .then((res) => {
          // console.log(res)
          setOpen(true);
          setSeverity("success");
          setAlart("status change successfully requested");
          setSingleTask(false);
          return;
        })
        .catch((err) => {
          // console.log(err)
          setOpen(true);
          setSeverity("error");
          setAlart("Error occured");
        });
    }
  };

  const alertClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpen(false);
  };

  const importExcel = (e) => {
    const file = e.target.files[0];

    setErrors({
      ...errors,
      file: null,
    });

    const reader = new FileReader();
    reader.onload = (e) => {
      //parse data
      const bstr = e.target.result;
      const workBook = XLSX.read(bstr, { type: "binary" });

      //get First Sheet
      const workSheetName = workBook.SheetNames[0];
      const workSheet = workBook.Sheets[workSheetName];
      //convert to array
      const fileData = XLSX.utils.sheet_to_json(workSheet, { header: 1 });
      const header = fileData[0];
      const headers = [];
      let err = { iserror: false, msg: "" };
      if (
        fileData[0][0] === "Task ID" &&
        fileData[0][2] === "Site ID" &&
        fileData[0][3] === "Site Name"
      ) {
        for (var i = 1; i < fileData.length; i++) {
          let temp = items.filter((it) => fileData[i][0] == it.taskRef);
          if (temp.length == 0) {
            err = {
              iserror: true,
              msg: `Please check the Task ID: ${fileData[i][0]}`,
            };
            break;
          }
          if (!arrayEquals(temp[0].headerProperties, header.slice(1))) {
            err = { iserror: true, msg: `Please check the header` };
            break;
          }
          // console.log(header)

          if (fileData[i][2] != temp[0].siteID) {
            err = {
              iserror: true,
              msg: `Please check the Site ID of Task : ${fileData[i][0]}`,
            };
            break;
          }
          if (fileData[i][3] != temp[0].siteName) {
            err = {
              iserror: true,
              msg: `Please check the Site Name of Task : ${fileData[i][0]}`,
            };
            break;
          }
          if (fileData[i].length != temp[0].properties.length + 1) {
            err = {
              iserror: true,
              msg: `Please check the number of attributes : ${fileData[i][0]}`,
            };
            break;
          }
        }
        if (!err.iserror) {
          for (var i = 0; i < fileData[0].length; i++) {
            let element = {
              field: fileData[0][i],
              headerName: fileData[0][i],
              minWidth: 150,
              flex: 1,
            };
            headers.push(element);
          }
          setHeadersConfig(headers);

          fileData.splice(0, 1);
          setNewProperties(fileData);
          setRates(convertToJson(header, fileData));
        } else {
          fileRef.current.value = "";
          setOpen(true);
          setSeverity("error");
          setAlart(err.msg);
          setRates([]);
          setHeadersConfig([]);
        }
      } else {
        fileRef.current.value = "";
        setOpen(true);
        setSeverity("error");
        setAlart(
          "Task ID, Site ID and Site Name should be in Column No 1, 3 and 4"
        );
        setRates([]);
        setHeadersConfig([]);
      }
    };
    if (file) {
      if (getExtention(file)) {
        reader.readAsBinaryString(file);
      } else {
        alert("Invalid file");
      }
    } else {
      setRates([]);
    }
  };
  const convertToJson = (headers, fileData) => {
    const rows = [];
    fileData.forEach((row, i) => {
      let rowData = {};
      rowData["id"] = i;
      row.forEach((element, index) => {
        rowData[headers[index]] = element;
      });
      rows.push(rowData);
    });
    return rows;
  };

  const getExtention = (file) => {
    const parts = file.name.split(".");
    const extension = parts[parts.length - 1];
    return EXTENSIONS.includes(extension);
  };

  const columns = [
    { field: "taskRef", headerName: "TaskID", width: 250 },
    {
      field: "SiteID",
      headerName: "SiteID",
      width: 150,
      valueGetter: (items) => items.row.properties[1],
    },
    {
      field: "Site Name",
      headerName: "Site Name",
      width: 175,
      valueGetter: (items) => items.row.properties[2],
    },
    { field: "project", headerName: "Project", width: 175 },
    { field: "subProject", headerName: "Sub Project", width: 175 },
    {
      field: "Change the Handover",
      width: 250,
      align: "center",
      renderCell: (cellValues) => {
        return (
          <Button
            variant="primary"
            style={{ width: 100 }}
            onClick={() => {
              getProjectManager(cellValues.row.assignedProjectManager);

              handleSingleTask(cellValues.row);
            }}
          >
            Edit
          </Button>
        );
      },
    },
  ];

  return (
    <div>
      {/* Content Wrapper. Contains page content */}
      <div className="content-wrapper">
        <div className="content-header">
          <section className="content-header">
            <div className="container-fluid">
              <div>
                <div>
                  <Container>
                    <Row>
                      <Col xs={4}>
                        <h1>Request Handover Changes</h1>
                      </Col>

                      <Col xs={2}></Col>

                      <Col>
                        <div className="vr" />
                      </Col>
                      <Col>
                        <div className="vr" />
                      </Col>
                      <Col>
                        <Button
                          variant="primary"
                          type="button"
                          onClick={() => setBulkTask(true)}
                        >
                          Handover Bulk Changes
                        </Button>
                      </Col>
                    </Row>
                  </Container>
                </div>
              </div>
            </div>
            {/* /.container-fluid */}
          </section>
        </div>

        <div style={{ height: 610, width: "100%" }}>
          <DataGrid
            getRowId={(item) => item._id}
            rows={items}
            columns={columns}
            pageSize={15}
            rowsPerPageOptions={[15]}
            rowHeight={30}
            headerHeight={40}
            components={{ Toolbar: GridToolbar }}
          />
        </div>
        {/* View excel */}
      </div>
      <Modal
        show={bulkTask}
        onHide={bulkHandleClose}
        animation={false}
        dialogClassName="bulk-modal-size"
      >
        <Modal.Header closeButton>
          <Modal.Title>Edit Handover</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Row className="mb-3">
              <Form.Group as={Col}>
                <Form.Label>Upload edited task handover</Form.Label>
                <Form.Control
                  onChange={importExcel}
                  ref={fileRef}
                  type="file"
                  isInvalid={!!errors.file}
                />
                <Form.Control.Feedback type="invalid">
                  {errors.file}
                </Form.Control.Feedback>
              </Form.Group>
            </Row>

            <Row>
              <div style={{ height: 610, width: "100%" }}>
                <DataGrid
                  getRowId={(item) => item.id}
                  rows={rates}
                  columns={headersConfig}
                  pageSize={15}
                  rowsPerPageOptions={[15]}
                  rowHeight={30}
                  headerHeight={40}
                  components={{ Toolbar: GridToolbar }}
                />
              </div>
            </Row>
            <Row>
              <Form.Group className="m-3" controlId="refid" as={Col}>
                <Form.Label>Reason for the change request</Form.Label>
                <Form.Control
                  as="textarea"
                  rows={3}
                  onChange={(e) => setComment(e.target.value)}
                  isInvalid={!!errors.comment}
                />
                <Form.Control.Feedback type="invalid">
                  Please provide comment
                </Form.Control.Feedback>
              </Form.Group>
            </Row>

            <Modal.Footer>
              <Button variant="secondary" onClick={bulkHandleClose}>
                Close
              </Button>
              <Button variant="primary" onClick={bulkSubmit}>
                Request approval
              </Button>
            </Modal.Footer>
          </Form>
        </Modal.Body>
      </Modal>

      {/* // */}

      <Modal show={singleTask} onHide={handleClose} size="lg" animation={false}>
        <Modal.Header closeButton>
          <Modal.Title>Edit Handover</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            {headerProperties?.map((headerProperty, index) => {
              if (index < 3) {
                return (
                  <Row>
                    <Form.Group className="m-2" controlId="refid" as={Col}>
                      <Form.Label>{headerProperty}</Form.Label>
                      <Form.Control
                        className="mr-1 ml-1"
                        value={properties[index]}
                        onChange={(e) => handleChange(e, index)}
                        type="text"
                        readOnly={true}
                      />
                    </Form.Group>
                  </Row>
                );
              } else {
                return (
                  <Row>
                    <Form.Group className="m-2" controlId="refid" as={Col}>
                      <Form.Label>{headerProperty}</Form.Label>
                      <Form.Control
                        value={properties[index]}
                        onChange={(e) => handleChange(e, index)}
                        type="text"
                      />
                    </Form.Group>
                  </Row>
                );
              }
            })}
            <Row>
              <Form.Group className="m-3" controlId="refid" as={Col}>
                <Form.Label>Reason for the change request</Form.Label>
                <Form.Control
                  as="textarea"
                  value={comment}
                  rows={3}
                  onChange={(e) => setComment(e.target.value)}
                  isInvalid={!!errors.comment}
                />
                <Form.Control.Feedback type="invalid">
                  Please provide comment
                </Form.Control.Feedback>
              </Form.Group>
            </Row>

            <Row>
              <Form.Group className="m-3" controlId="refid" as={Col}>
                <Form.Label> Change Handover Approver</Form.Label>
                <Form.Control
                  as="select"
                  defaultValue={item.taskStatus}
                  onChange={(e) => setHandoverChangeApprover(e.target.value)}
                >
                  <option value="">Choose...</option>
                  {mobitelUsersName.map((user) => (
                    <option key={user._id} value={user._id}>
                      {user.name}
                    </option>
                  ))}
                </Form.Control>
              </Form.Group>
            </Row>
            <Modal.Footer>
              <Button variant="primary" onClick={handleSubmit}>
                Request approval
              </Button>
            </Modal.Footer>
          </Form>

          <Row>
            <Form.Group className="m-3" controlId="refid" as={Col}>
              <Form.Label>Change Status</Form.Label>
              <Form.Control
                as="select"
                defaultValue={item.taskStatus}
                onChange={(e) => setStatus(e.target.value)}
              >
                <option hidden value={item.taskStatus}>
                  {item.taskStatus}
                </option>
                <option value="Withdraw">Withdraw</option>
                {item?.taskStatus == "Hold" ? (
                  <option value={continuesStatus}>Continue...</option>
                ) : (
                  <option value="Hold">Hold</option>
                )}
              </Form.Control>
            </Form.Group>
          </Row>
          <Row>
            <Form.Group className="m-3" controlId="refid" as={Col}>
              <Form.Label> Change Handover Approver</Form.Label>
              <Form.Control
                as="select"
                defaultValue={item.taskStatus}
                onChange={(e) => setHandoverChangeApprover(e.target.value)}
              >
                <option value="">Choose...</option>
                {mobitelUsersName.map((user) => (
                  <option key={user._id} value={user._id}>
                    {user.name}
                  </option>
                ))}
              </Form.Control>
            </Form.Group>
          </Row>

          <Row>
            <Form.Group className="m-3" controlId="refid" as={Col}>
              <Form.Label>Reason for the Status Change</Form.Label>
              <Form.Control
                as="textarea"
                rows={3}
                value={commentStatus}
                onChange={(e) => setCommentStatus(e.target.value)}
                isInvalid={!!errors.commentStatus}
              />
              <Form.Control.Feedback type="invalid">
                Please provide comment
              </Form.Control.Feedback>
            </Form.Group>
          </Row>
          <Row>
            <Form.Group className="m-3" controlId="refid" as={Col}>
              <Form.Label>Project Manager</Form.Label>
              {" - " + projectManager}
            </Form.Group>
          </Row>
        </Modal.Body>

        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button
            variant="primary"
            onClick={changeStatus}
            disabled={item.taskStatus === status}
          >
            Change Status
          </Button>
        </Modal.Footer>
      </Modal>
      <Snackbar
        open={open}
        autoHideDuration={6000}
        onClose={alertClose}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert onClose={alertClose} severity={severity} sx={{ width: "100%" }}>
          {alart}
        </Alert>
      </Snackbar>

      <Dialog
        open={openDialog}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          <Box sx={{ display: "flex" }}>
            <CircularProgress />
          </Box>
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default withRouter(RequestHandoverChangesBody);
